.section-home {
  flex: 1;
  display: flex;
  flex-direction: column;
  width: 95vw;
  margin: 0 auto;
  margin-bottom: 0.6rem;
  position: relative;
  padding: 0.125rem;
  border: 1px solid red;
}

.section-center {
  flex: 1;
  display: grid;
  grid-template-columns: 0.5fr 2fr 0.5fr;
  /* width: 100%; */
  /* height: 100%; */
  gap: 4px;
  position: relative;
  /* border: 1px solid blue; */
}
.home-article {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 100px;
  /* border: 1px solid red; */
}
.img-center {
  flex: 1;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
.img-center img {
  position: absolute;
  width: 100%;
  height: 100%;
  border: 1px solid red;
}
.home-article img {
  /* width: 100px; */
}

.img-title {
  /* border: 1px solid blue; */
  /* position: relative; */
}
.img-title h3 {
  color: #fff;
}
.home-title {
  /* position: absolute; */
}
.img-title {
  /* border: 1px solid blue; */
}
.section-center .activeSlide .img-title > h3 {
  /* border: 1px solid red; */
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 2rem;
  border: 1px solid blue;
}
.hiddenSlide {
  display: none;
}
.lastSlide .img-title,
.nextSlide .img-title {
  visibility: hidden;
}
.lastSlide {
  order: 1;
}
.activeSlide {
  order: 2;
}
.nextSlide {
  order: 3;
}
